import React, { useState, useRef } from "react";
import { send } from "emailjs-com";
import "./ErrorReportModal.scss";

const ErrorReportModal = ({ isOpen, onClose }) => {
  const [formData, setFormData] = useState({
    from_name: "", //   ( )
    from_email: "", //   ()
    message: "", //  
  });

  const overlayRef = useRef(null); //     ref

  const handleOverlayClick = (e) => {
    if (e.target === overlayRef.current) {
      onClose();
    }
  };

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const { from_name, from_email, message } = formData;

    try {
      await send(
        "service_solfany", // EmailJS  ID
        "template_gwxjjr9", //  ID
        {
          from_name: from_name || " ",
          from_email: from_email || "default@example.com",
          my_name: "", // 
          message: message || "( )",
          to_email: "solfany1999@gmail.com", // 
        },
        "VvQSChtbcKA2xvnoK" // Public Key
      );
      alert("   .");
      setFormData({ from_name: "", from_email: "", message: "" });
      onClose();
    } catch (error) {
      console.error("  :", error);
      alert("  .");
    }
  };

  if (!isOpen) return null;

  return (
    <div
      className="error-modal-overlay"
      ref={overlayRef}
      onClick={handleOverlayClick}
    >
      <div className="error-modal">
        <div className="error-modal__header">
          <h2 className="error-modal__header-title"> </h2>
          <button className="error-modal__header-close" onClick={onClose}>
            
          </button>
        </div>

        <div className="error-modal__content">
          <p className="dining-modal__description">
                   .
          </p>
          <form onSubmit={handleSubmit}>
            <input
              className="error-modal__input"
              name="from_name"
              placeholder=" "
              value={formData.from_name}
              onChange={handleChange}
            />
            <input
              className="error-modal__input"
              name="from_email"
              placeholder="  ()"
              value={formData.from_email}
              onChange={handleChange}
            />
            <textarea
              className="error-modal__input error-modal__textarea"
              name="message"
              placeholder="  "
              value={formData.message}
              onChange={handleChange}
            />

            <div className="error-modal__button-group">
              <button type="submit"></button>
              <button type="button" onClick={onClose}>
                
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default ErrorReportModal;
